* {
  box-sizing: border-box;
}

body {
  font-family: "Roboto";
  font-size: 1rem;
}

a {
  text-decoration: none;
}

p {
  font-size: 1.5rem;
}

h1 {
  font-size: 2.5rem;
  line-height: 3rem;
}

h2 {
  font-size: 2rem;
  line-height: 2.5rem;
}

@media screen and (max-width: 480px) {
  p {
    font-size: 1rem;
  }

  h1 {
    font-size: 1.725rem;
    line-height: 2rem;
  }

  h2 {
    font-size: 1.5rem;
    line-height: 1.8rem;
  }
}

.t-italic {
  font-style: italic;
}

.page-root {
  padding: 16px;
  max-width: 640px;
  min-width: 300px;
  margin: 0 auto;
  display: flex;
  padding-bottom: 60px;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
}

@media screen and (max-width: 480px) {
  .page-root {
    max-width: 420px;
  }
}

.page-info {
  padding-bottom: 60px;
}

.page-description {
  font-size: 1.25rem;
  margin: 12px 0;
}

.form-column {
  margin: 24px 0 40px 0;
}

.button-row {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 20px;
}